export function getOffset(value,min,max){return(value-min)/(max-min)}export function getDirectionStyle(direction,value,min,max){var offset=getOffset(value,min,max),positionStyle={};switch(direction){case"rtl":positionStyle.right="".concat(100*offset,"%"),positionStyle.transform="translateX(50%)";break;case"btt":positionStyle.bottom="".concat(100*offset,"%"),positionStyle.transform="translateY(50%)";break;case"ttb":positionStyle.top="".concat(100*offset,"%"),positionStyle.transform="translateY(-50%)";break;default:positionStyle.left="".concat(100*offset,"%"),positionStyle.transform="translateX(-50%)"}return positionStyle}export function getIndex(value,index){return Array.isArray(value)?value[index]:value}